<template>
  <AppLayout class="item-page">
    <template v-slot:default>
      <FormsHeader class="item-page__forms-header" :form-id="itemId" />

      <VTabs
        :items="tabsItems"
        :current-tab="currentTab"
        @change="changeCurrentTab"
      />

      <transition name="item-page__forms" mode="out-in">
        <GeneralForm v-if="currentTab === 'general'" />

        <AuthorsForm v-else-if="currentTab === 'authors'" />
      </transition>
    </template>

    <template v-slot:footer>
      <ItemPageFooter />
    </template>
  </AppLayout>
</template>

<script>
import VTabs from "@/components/tabs/Tabs";
import AppLayout from "@/components/app/AppLayout";
import GeneralForm from "@/components/forms/GeneralForm";
import FormsHeader from "@/components/forms/FormsHeader";
import { FormsMutations } from "@/store/modules/forms/mutations";
import ItemPageFooter from "@/components/item-page/ItemPageFooter";
import { FORMS_TABS_ITEMS, FormsStatus } from "@/constants/FormConstants";
import AuthorsForm from "@/components/authors/AuthorsForm";

export default {
  name: "ItemView",
  components: {
    AuthorsForm,
    FormsHeader,
    ItemPageFooter,
    AppLayout,
    GeneralForm,
    VTabs,
  },
  computed: {
    itemId() {
      const id = Number(this.$route.params.id);

      if (!Number.isNaN(id) && id !== 0) {
        return id;
      }

      return FormsStatus.Draft;
    },
    currentTab() {
      return this.$route.query.tab || "general";
    },
    isLoading() {
      if (this.itemId > 0) {
        return this.$store.getters.isFormsItemFetching(this.itemId);
      }

      return false;
    },
    data() {
      if (this.itemId) {
        return this.$store.getters.getFormsItem(this.itemId);
      }

      return this.$store.getters.getFormsDraftItem;
    },
  },
  data() {
    return {
      tabsItems: FORMS_TABS_ITEMS,
    };
  },
  mounted() {
    this.$store.commit(FormsMutations.CreateDraftForm);

    if (this.$route.query.type && this.itemId === FormsStatus.Draft) {
      this.$store.commit(FormsMutations.ChangeFormsType, {
        id: FormsStatus.Draft,
        type: this.$route.query.type,
      });
      this.$router.replace({ ...this.$route, query: {} });
    }
  },
  methods: {
    changeCurrentTab(tab) {
      this.$router.replace({ ...this.$route, query: { tab } });
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";

.item-page__forms-header {
  margin: 19px 0 29px;
}

.item-page__forms-enter-active,
.item-page__forms-leave-active {
  @include transition(opacity);
}

.item-page__forms-enter-from,
.item-page__forms-leave-to {
  opacity: 0;
}
</style>
