<template>
  <div class="radio-group">
    <VButton
      :key="item.key"
      v-for="item in items"
      class="radio-group__item"
      @click="$emit('update:modelValue', item.key)"
      :class="{ 'radio-group__item--active': item.key === modelValue }"
    >
      <div class="radio-group__item-dot" />

      <VTypography font-weight="medium" class="radio-group__item-title">{{
        item.title
      }}</VTypography>
    </VButton>
  </div>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";

export default {
  name: "RadioGroup",
  components: { VButton, VTypography },
  props: {
    items: Array,
    modelValue: String,
  },
};
</script>

