<template>
  <table class="v-table">
    <thead>
      <tr class="v-table__row v-table__header-row">
        <th
          :key="item"
          class="v-table__cell"
          v-for="item in headers"
          :style="{ width: `${item.width}px` }"
        >
          {{ item.title }}
        </th>
        <th
          v-if="hasActions"
          class="v-table__cell"
          :style="{ width: `${actionsWidth}px` }"
        >
          Действия
        </th>
      </tr>
    </thead>
    <tbody>
      <tr
        :key="item"
        v-for="(item, index) in items"
        class="v-table__row v-table__body-row"
      >
        <td
          :key="key"
          class="v-table__cell"
          v-for="(_, key) in item"
          :style="{
            width: `${headers[key]?.width}px`,
            textAlign: `${headers[key]?.align}`,
          }"
        >
          {{ item[key] }}
        </td>

        <td
          v-if="hasActions"
          class="v-table__cell"
          :style="{ width: `${actionsWidth}px` }"
        >
          <slot name="actions" :item="item" :index="index"></slot>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: "VTable",
  data() {
    return {
      headers: [],
    };
  },
  props: {
    items: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    hasActions() {
      return !!this.$slots.actions;
    },
    actionsWidth() {
      const actions = this.$slots.actions ? this.$slots.actions() : [];

      console.log(">>>>>>", actions);

      if (actions.length > 0) {
        return actions[0].props.width;
      }

      return 0;
    },
  },
  mounted() {
    const columns = this.$slots.default ? this.$slots.default() || [] : [];

    this.headers = columns.reduce((acc, item) => {
      return {
        ...acc,
        [item.props.name]: { ...item.props },
      };
    }, {});
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.v-table {
  border-collapse: collapse;
}

.v-table__row {
  background-color: $gray5;

  &:not(:last-child) {
    border-bottom: 2px solid $white;
  }
}

.v-table__body-row {
  &:hover {
    background-color: $gray6;
  }
}

.v-table__header-row {
  white-space: nowrap;
  border-bottom: 2px solid $white;
}

.v-table__cell {
  padding: 12px 15px;

  &:not(:last-child) {
    border-right: 2px solid $white;
  }
}
</style>
