<template>
  <div class="item-page-legend-item">
    <div class="item-page-legend-item__indicator" :class="statusClasses" />

    <VTypography>{{ title }}</VTypography>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";

export default {
  name: "ItemPageLegendItem",
  components: { VTypography },
  props: {
    status: {
      type: String,
      default: "not-filled",
      validator(value) {
        return ["filled", "not-filled", "not-fully-filled"].includes(value);
      },
    },
  },
  computed: {
    title() {
      switch (this.status) {
        case "filled":
          return "Заполнено";
        case "not-fully-filled":
          return "Заполнено не полностью";

        default:
          return "Не заполнено";
      }
    },
    statusClasses() {
      return {
        "item-page-legend-item__indicator--filled": this.status === "filled",
        "item-page-legend-item__indicator--not-filled":
          this.status === "not-filled",
        "item-page-legend-item__indicator--not-fully-filled":
          this.status === "not-fully-filled",
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.item-page-legend-item {
  display: flex;
  align-items: center;
}

.item-page-legend-item__indicator {
  width: 58px;
  height: 2px;
  margin-right: 15px;
  background-color: $gray5;
}

.item-page-legend-item__indicator--filled {
  background-color: $green;
}

.item-page-legend-item__indicator--not-fully-filled {
  background-color: $yellow;
}
</style>
