<template>
  <VModal class="author-modal" position="right">
    <ModalContainer title="Добавить автора" @close="$emit('close', $event)">
      <template v-slot:content>
        <TextInput
          label="ФИО"
          type="contrast"
          :maxlength="100"
          :error="errors.russianName"
          placeholder="Русский текст"
          v-model="author.russianName"
          root-class="author-modal__field"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          :error="errors.englishName"
          v-model="author.englishName"
          placeholder="Английский текст"
        />
      </template>

      <template v-slot:footer>
        <ActionButton type="outline" @click="onSubmit">{{
          buttonTitle
        }}</ActionButton>
      </template>
    </ModalContainer>
  </VModal>
</template>

<script>
import VModal from "@/components/ui/Modal";
import TextInput from "@/components/ui/TextInput";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";

export default {
  name: "AuthorModal",
  components: { TextInput, ActionButton, ModalContainer, VModal },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    initialValues: {
      type: Object,
      default: () => ({
        russianName: "",
        englishName: "",
      }),
    },
  },
  data() {
    return {
      errors: {},
      author: this.initialValues,
    };
  },
  computed: {
    buttonTitle() {
      return this.initialValues.russianName || this.initialValues.englishName
        ? "Изменить автора"
        : "Добавить автора";
    },
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();

      this.errors = {};

      const hasRussianName = !!this.author.russianName;
      const hasEnglishName = !!this.author.englishName;

      if (!hasRussianName) {
        this.errors = {
          russianName: "Введите имя на русском языке",
        };
      }

      if (!hasEnglishName) {
        this.errors = {
          ...this.errors,
          englishName: "Введите имя на английском языке",
        };
      }

      if (hasRussianName && hasEnglishName) {
        this.$emit("submit", this.author);
        this.author = {};
      }
    },
  },
};
</script>

<style scoped lang="scss">
.author-modal {
  justify-content: flex-end;
}

.author-modal__field {
  margin-bottom: 15px;
}
</style>
