<template>
  <teleport to="body">
    <transition name="v-modal">
      <div
        v-if="show"
        role="dialog"
        v-bind="$attrs"
        class="v-modal"
        aria-modal="true"
        :class="rootClasses"
        aria-labelledby="modal-headline"
      >
        <slot></slot>
      </div>
    </transition>
  </teleport>
</template>

<script>
export default {
  name: "VModal",
  inheritAttrs: false,
  props: {
    show: Boolean,
    position: {
      type: String,
      default: "center",
      validator(value) {
        return ["left", "center", "right"].includes(value);
      },
    },
  },
  computed: {
    rootClasses() {
      return {
        "v-modal--left": this.position === "left",
        "v-modal--right": this.position === "right",
        "v-modal--center": this.position === "center",
      };
    },
  },
  watch: {
    show(value) {
      if (value) {
        document.body.style = "overflow:hidden";
      } else {
        document.body.style = "";
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";

.v-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
}

.v-modal--center {
  justify-content: center;
}

.v-modal--left {
  justify-content: flex-start;
}

.v-modal--right {
  justify-content: flex-end;
}

.v-modal-enter-active,
.v-modal-leave-active {
  @include transition(opacity);
}

.v-modal-enter-from,
.v-modal-leave-to {
  opacity: 0;
}
</style>
