<template>
  <FormsContainer class="general-form">
    <VTypography
      font-weight="heavy"
      font-style="header-1"
      class="general-form__header"
      >Общая информация</VTypography
    >

    <div class="general-form__row">
      <div class="general-form__col">
        <TextInput
          label="Направлено"
          id="DirectedInputId"
          :info-popover-width="266"
          placeholder="Русский текст"
          v-model="form.russianInformation"
          info-text="Фамилия Имя Отчество полностью"
        />
      </div>
      <div class="general-form__col">
        <TextInput
          placeholder="Английский текст"
          v-model="form.englishInformation"
        />
      </div>
    </div>
  </FormsContainer>
</template>

<script>
import TextInput from "@/components/ui/TextInput";
import VTypography from "@/components/ui/Typography";
import { FormsGetters } from "@/store/modules/forms/getters";
import FormsContainer from "@/components/forms/FormsContainer";
import { FormsMutations } from "@/store/modules/forms/mutations";

export default {
  name: "GeneralForm",
  components: { FormsContainer, VTypography, TextInput },
  props: {
    formId: [String, Number],
  },
  computed: {
    form() {
      return (
        this.$store.getters[FormsGetters.GetGeneralForm](this.formId) || {}
      );
    },
  },
  beforeUnmount() {
    const { ...form } = this.form;

    this.$store.commit(FormsMutations.UpdateGeneralFormMutation, {
      form,
      id: this.formId,
    });
  },
};
</script>

<style scoped lang="scss">
.general-form__header {
  margin-bottom: 35px;
}

.general-form__row {
  display: flex;
}

.general-form__col {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  &:not(:last-child) {
    margin-right: 13px;
  }

  &:not(:first-child) {
    margin-left: 13px;
  }
}
</style>
