<template>
  <FormsContainer class="authors-form">
    <VTypography
      font-weight="heavy"
      font-style="header-1"
      class="authors-form__header"
      >Авторы технологии</VTypography
    >

    <TypographyWithInfo
      font-weight="heavy"
      :info-popover-width="529"
      class="authors-form__title"
      info-text="Просим обратить внимание, что автор — это человек, творческим трудом которого была создана Технология. Оказание технической, финансовой, организационной или материальной поддержки не является творческим трудом и не делает лицо, оказавшее такую помощь, Автором.
В случае патентования (регистрации) Технологии персональные данные Авторов будут предоставлены патентным поверенным (на основании соглашения о неразглашении) и регистрационным органам."
    >
      Авторы Института
    </TypographyWithInfo>

    <AuthorsTable class="authors-form__authors-table" />

    <TypographyWithInfo
      font-weight="heavy"
      :info-popover-width="529"
      class="authors-form__title"
      info-text="Укажите в таблице сведения об Авторах, не являющихся Авторами Института ниже. Если в создании Технологии иные Авторы участия не принимали, поставьте отметку «Не привлекались»"
    >
      Иные Авторы
    </TypographyWithInfo>

    <RadioGroup
      class="authors-form__attracted-radio"
      v-model="attractedOtherAuthors"
      :items="[
        { key: 'attracted', title: 'Привлекались' },
        { key: 'not-attracted', title: 'Не привлекались' },
      ]"
    />

    <OtherAuthorsTable
      v-if="attractedOtherAuthors === 'attracted'"
      class="authors-form__authors-table"
    />
  </FormsContainer>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import AuthorsTable from "@/components/authors/AuthorsTable";
import FormsContainer from "@/components/forms/FormsContainer";
import { FormsMutations } from "@/store/modules/forms/mutations";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import RadioGroup from "@/components/ui/RadioGroup";
import OtherAuthorsTable from "@/components/authors/OtherAuthorsTable";

export default {
  name: "AuthorsForm",
  components: {
    OtherAuthorsTable,
    RadioGroup,
    VTypography,
    AuthorsTable,
    FormsContainer,
    TypographyWithInfo,
  },
  data() {
    return {
      showModal: false,
      attractedOtherAuthors: "attracted",
    };
  },
  computed: {
    items() {
      const list = [{ russianName: "dsfsdfsdf", englishName: "sdfddddd" }]; // this.$store.getters[FormsGetters.GetInstituteAuthors];

      return list.map((x, idx) => ({
        number: idx + 1,
        value: `${x.russianName}/${x.englishName} `,
      }));
    },
  },
  methods: {
    onClick() {
      this.showModal = true;
    },
    onClose() {
      this.showModal = false;
    },
    onSubmit(values) {
      this.$store.commit(FormsMutations.AddAuthorMutation, {
        id: "draft",
        data: values,
      });
      this.showModal = false;
    },
  },
};
</script>

<style scoped lang="scss">
.authors-form__header {
  margin-bottom: 35px;
}

.authors-form__title {
  margin-bottom: 37px;
}

.authors-form__authors-table {
  margin-bottom: 100px;
}

.authors-form__attracted-radio {
  margin-bottom: 35px;
}
</style>
