<template>
  <VModal class="other-author-modal" position="right">
    <ModalContainer
      title="Добавить иного автора"
      @close="$emit('close', $event)"
    >
      <template v-slot:content>
        <TextInput
          label="ФИО"
          type="contrast"
          :maxlength="100"
          popover-color="#272726"
          :info-popover-width="266"
          :error="errors.russianName"
          placeholder="Русский текст"
          v-model="author.russianName"
          root-class="other-author-modal__field"
          info-text="Какой-то будет текст подсказки"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          :error="errors.englishName"
          v-model="author.englishName"
          placeholder="Английский текст"
          root-class="other-author-modal__last-group-field"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          label="Место работы"
          popover-color="#272726"
          :info-popover-width="266"
          :error="errors.russianWorkplace"
          placeholder="Русский текст"
          v-model="author.russianWorkplace"
          root-class="other-author-modal__field"
          info-text="Какой-то будет текст подсказки"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          :error="errors.englishWorkplace"
          v-model="author.englishWorkplace"
          placeholder="Английский текст"
          root-class="other-author-modal__last-group-field"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          label="Подразделение"
          popover-color="#272726"
          :info-popover-width="266"
          :error="errors.russianDivision"
          placeholder="Русский текст"
          v-model="author.russianDivision"
          root-class="other-author-modal__field"
          info-text="Какой-то будет текст подсказки"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          :error="errors.englishDivision"
          v-model="author.englishDivision"
          placeholder="Английский текст"
          root-class="other-author-modal__last-group-field"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          label="Должность"
          popover-color="#272726"
          :info-popover-width="266"
          :error="errors.russianPosition"
          placeholder="Русский текст"
          v-model="author.russianPosition"
          root-class="other-author-modal__field"
          info-text="Какой-то будет текст подсказки"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          :error="errors.englishPosition"
          v-model="author.englishPosition"
          placeholder="Английский текст"
          root-class="other-author-modal__last-group-field"
        />

        <TypographyWithInfo
          :info-popover-width="266"
          :style="{ color: '#ffffff', marginBottom: '10px' }"
          info-text="Какой-то будет текст подсказки"
          >Гражданство</TypographyWithInfo
        >

        <RadioGroup
          v-model="citizenship"
          :items="[
            { key: 'rf', title: 'РФ' },
            { key: 'other', title: 'Другое' },
          ]"
          :style="{ marginBottom: citizenship === 'other' ? '10px' : '35px' }"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          placeholder="Русский текст"
          v-if="citizenship === 'other'"
          root-class="other-author-modal__field"
          :error="errors.russianOtherCitizenship"
          v-model="author.russianOtherCitizenship"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          v-if="citizenship === 'other'"
          :error="errors.englishOtherCitizenship"
          v-model="author.englishOtherCitizenship"
          placeholder="Английский текст"
          root-class="other-author-modal__last-group-field"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          label="Зарегистрирован по адресу"
          popover-color="#272726"
          :info-popover-width="266"
          :error="errors.russianRegistration"
          placeholder="Русский текст"
          v-model="author.russianRegistration"
          root-class="other-author-modal__field"
          info-text="Какой-то будет текст подсказки"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          :error="errors.englishRegistration"
          v-model="author.englishRegistration"
          placeholder="Английский текст"
          root-class="other-author-modal__last-group-field"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          label="Почтовый адрес"
          popover-color="#272726"
          :info-popover-width="266"
          :error="errors.russianPostal"
          placeholder="Русский текст"
          v-model="author.russianPostal"
          root-class="other-author-modal__field"
          info-text="Какой-то будет текст подсказки"
        />

        <TextInput
          type="contrast"
          :maxlength="100"
          :error="errors.englishPostal"
          v-model="author.englishPostal"
          placeholder="Английский текст"
          root-class="other-author-modal__last-group-field"
        />
      </template>

      <template v-slot:footer>
        <ActionButton type="outline" @click="onSubmit">{{
          buttonTitle
        }}</ActionButton>
      </template>
    </ModalContainer>
  </VModal>
</template>

<script>
import VModal from "@/components/ui/Modal";
import TextInput from "@/components/ui/TextInput";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import RadioGroup from "@/components/ui/RadioGroup";

export default {
  name: "OtherAuthorModal",
  components: {
    RadioGroup,
    TypographyWithInfo,
    TextInput,
    ActionButton,
    ModalContainer,
    VModal,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    initialValues: {
      type: Object,
      default: () => ({
        russianName: "",
        englishName: "",
      }),
    },
  },
  data() {
    return {
      errors: {},
      author: this.initialValues,
      citizenship: "rf",
    };
  },
  computed: {
    buttonTitle() {
      return this.initialValues.russianName || this.initialValues.englishName
        ? "Изменить автора"
        : "Добавить автора";
    },
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();

      this.errors = {};

      const hasRussianName = !!this.author.russianName;
      const hasEnglishName = !!this.author.englishName;

      if (!hasRussianName) {
        this.errors = {
          russianName: "Введите имя на русском языке",
        };
      }

      if (!hasEnglishName) {
        this.errors = {
          ...this.errors,
          englishName: "Введите имя на английском языке",
        };
      }

      if (hasRussianName && hasEnglishName) {
        this.$emit("submit", this.author);
        this.author = {};
      }
    },
  },
};
</script>

<style scoped lang="scss">
.other-author-modal {
  justify-content: flex-end;
}

.other-author-modal__field {
  margin-bottom: 15px;
}

.other-author-modal__last-group-field {
  margin-bottom: 35px;
}
</style>
