<template>
  <VButton class="tabs-item" :class="rootClasses">
    <VTypography class="tabs-item__title">{{ title }}</VTypography>
  </VButton>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";

export default {
  name: "TabsItem",
  components: { VButton, VTypography },
  props: {
    title: {
      type: String,
      required: true,
    },
    active: {
      type: Boolean,
      required: true,
    },
    status: {
      type: String,
      default: "not-filled",
      validator(value) {
        return ["filled", "not-filled", "not-fully-filled"].includes(value);
      },
    },
  },
  computed: {
    rootClasses() {
      return {
        "tabs-item--active": this.active,
        "tabs-item--filled": this.status === "filled",
        "tabs-item--not-filled": this.status === "not-filled",
        "tabs-item--not-fully-filled": this.status === "not-fully-filled",
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.tabs-item {
  @include transition(border-bottom-color);
  height: 26px;
  display: flex;
  cursor: pointer;
  user-select: none;
  align-items: center;
  justify-content: center;
  border-bottom: 2px solid $gray5;

  &:not(:first-child) {
    margin-left: 15px;
  }

  &:not(:last-child) {
    margin-right: 15px;
  }

  &:hover {
    &:not(.tabs-item--filled):not(.tabs-item--not-fully-filled) {
      border-bottom-color: $gray1;
    }

    & > .tabs-item__title {
      color: $gray1;
    }
  }
}

.tabs-item__title {
  @include transition(color);
  color: $gray4;
  white-space: nowrap;
}

.tabs-item--active {
  border-bottom-color: $gray1;

  & > .tabs-item__title {
    color: $gray1;
  }
}

.tabs-item--filled {
  border-bottom-color: $green;
}

.tabs-item--not-fully-filled {
  border-bottom-color: $yellow;
}
</style>
