<template>
  <div class="authors-table">
    <VTable :items="items" class="authors-table__table">
      <VTableCol title="№" name="number" :width="61" align="center" />
      <VTableCol title="ФИО" name="value" />

      <template v-slot:actions="props">
        <VTableCol
          :width="100"
          name="actions"
          title="Действия"
          class="authors-table__actions-cell"
        >
          <EditButton
            @click="onEdit(props.index)"
            class="authors-table__edit-button"
          />
          <DeleteButton
            @click="onDelete(props.index)"
            class="authors-table__delete-button"
          />
        </VTableCol>
      </template>
    </VTable>

    <AddButton @click="onClick"> Добавить автора </AddButton>

    <AuthorModal @close="onClose" :show="showModal" @submit="onSubmit" />
  </div>
</template>

<script>
import VTable from "@/components/table/VTable";
import VTableCol from "@/components/table/VTableCol";
import AddButton from "@/components/buttons/AddButton";
import EditButton from "@/components/buttons/EditButton";
import AuthorModal from "@/components/authors/AuthorModal";
import DeleteButton from "@/components/buttons/DeleteButton";
import { FormsMutations } from "@/store/modules/forms/mutations";

export default {
  name: "AuthorsTable",
  components: {
    AddButton,
    DeleteButton,
    EditButton,
    VTable,
    VTableCol,
    AuthorModal,
  },
  data() {
    return {
      showModal: false,
    };
  },
  computed: {
    items() {
      const list = []; // this.$store.getters[FormsGetters.GetInstituteAuthors];

      return list.map((x, idx) => ({
        number: idx + 1,
        value: `${x.russianName}/${x.englishName} `,
      }));
    },
  },
  methods: {
    onClick() {
      this.showModal = true;
    },
    onClose() {
      this.showModal = false;
    },
    onSubmit(values) {
      this.$store.commit(FormsMutations.AddAuthorMutation, {
        id: "draft",
        data: values,
      });
      this.showModal = false;
    },
    onEdit(index) {
      console.log(`EDIT - ${index}`);
    },
    onDelete(index) {
      console.log(`DELETE - ${index}`);
    },
  },
};
</script>

