.authors-table {
  display: flex;
  flex-direction: column;
}

.authors-table__table {
  margin-bottom: 34px;
}

.authors-table__actions-cell {
  display: flex;
  align-items: center;
  justify-content: center;
}

.authors-table__edit-button {
  margin-right: 8px;
}

.authors-table__delete-button {
  margin-left: 8px;
}
</style>
