<template>
  <div class="v-tabs">
    <TabsItem
      :key="item.key"
      :title="item.title"
      v-for="item in items"
      :status="item.status"
      :active="currentTab === item.key"
      @click="$emit('change', item.key)"
    />
  </div>
</template>

<script>
import TabsItem from "@/components/tabs/TabsItem";

export default {
  name: "VTabs",
  components: { TabsItem },
  emits: {
    change: Function,
  },
  props: {
    currentTab: String,
    items: {
      type: Array,
      default() {
        return [];
      },
      validator(value) {
        return value.every((x) => !!x.key && !!x.title);
      },
    },
  },
};
</script>

