.v-tabs {
  overflow-x: auto;
  display: flex;
  width: 100%;

  &::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: none;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.25);
    outline: 1px solid rgba(0, 0, 0, 0.25);
  }
}
</style>
