<template>
  <VTypography
    class="v-status"
    font-style="header-1"
    font-weight="medium"
    :class="statusClasses"
    >•{{ text }}</VTypography
  >
</template>

<script>
import VTypography from "@/components/ui/Typography";
import { FormsStatus } from "@/constants/FormConstants";
import { formatStatusTitle } from "@/helpers/StatusHelpers";

export default {
  name: "FormsStatus",
  components: { VTypography },
  props: {
    status: {
      type: String,
      default: FormsStatus.Draft,
      validator(value) {
        return Object.values(FormsStatus).includes(value);
      },
    },
  },
  computed: {
    text() {
      return formatStatusTitle(this.status);
    },
    statusClasses() {
      return {
        "v-status--draft": this.status === FormsStatus.Draft,
        "v-status--signed": this.status === FormsStatus.Signed,
        "v-status--revision": this.status === FormsStatus.Revision,
        "v-status--accepted": this.status === FormsStatus.Accepted,
        "v-status--inspection": this.status === FormsStatus.Inspection,
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.v-status {
  text-transform: uppercase;
}

.v-status--draft {
  color: $gray4;
}

.v-status--inspection {
  color: $yellow;
}

.v-status--signed {
  color: $green;
}

.v-status--accepted {
  color: $purple;
}

.v-status--revision {
  color: $orange;
}
</style>
