<template>
  <div class="v-switch">
    <div
      v-if="!!modelValue"
      class="v-switch__indicator"
      :style="{ width, transform }"
    />

    <div class="v-switch__container">
      <VButton
        ref="buttons"
        :key="item.key"
        class="v-switch__item"
        v-for="item in items"
        @click="click(item.key)"
        :class="{ 'v-switch__item--active': modelValue === item.key }"
      >
        <VTypography font-weight="heavy" class="v-switch__item-title">{{
          item.title
        }}</VTypography>
      </VButton>
    </div>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import VButton from "@/components/buttons/VButton";

export default {
  name: "VSwitch",
  components: { VButton, VTypography },
  props: {
    items: Array,
    modelValue: String,
  },
  data() {
    return {
      width: 0,
      transform: "translateX(0)",
    };
  },
  mounted() {
    this.animate(0);
  },
  watch: {
    modelValue(value) {
      const valueIndex = this.items.findIndex((x) => x.key === value);

      const index = valueIndex >= 0 ? valueIndex : 0;

      this.animate(index);
    },
  },
  methods: {
    animate(index) {
      const prevIndex = index > 0 ? index - 1 : -1;

      const currentRef = this.$refs.buttons[index].$refs.vButton;
      const prevRef =
        prevIndex >= 0
          ? this.$refs.buttons[prevIndex].$refs.vButton
          : undefined;

      if (prevRef) {
        this.transform = `translateX(${prevRef?.clientWidth}px)`;
      } else {
        this.transform = "translateX(0)";
      }

      this.width = `${currentRef?.clientWidth + 2}px`;
    },
    click(key) {
      this.$emit("update:modelValue", key);
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.v-switch {
  height: 32px;
  display: flex;
  position: relative;
  flex-direction: column;
  border: 2px solid $gray1;
}

.v-switch__item {
  display: flex;
  cursor: pointer;
  padding: 0 12px;
  user-select: none;
  align-items: center;
  justify-content: center;

  &:not(:first-child) {
    border-left: 1px solid $gray1;
  }

  &:not(:last-child) {
    border-right: 1px solid $gray1;
  }
}

.v-switch__item--active {
  & > .v-switch__item-title {
    color: $white;
  }
}

.v-switch__item-title {
  @include transition(color);
  color: $gray1;
  text-transform: uppercase;
}

.v-switch__indicator {
  @include transition(transform);
  top: 0;
  bottom: 0;
  z-index: -1;
  position: absolute;
  background-color: $gray1;
}

.v-switch__container {
  flex: 1;
  display: flex;
}
</style>
