<template>
  <div class="forms-header">
    <div class="forms-header__file-name-container">
      <FormName :title="name" />

      <VStatus :status="status" />
    </div>

    <FormTypeSwitch v-model="type" />
  </div>
</template>

<script>
import FormName from "@/components/forms/FormName";
import VStatus from "@/components/forms/FormsStatus";
import { FormsGetters } from "@/store/modules/forms/getters";
import FormTypeSwitch from "@/components/forms/FormTypeSwitch";
import { FormsMutations } from "@/store/modules/forms/mutations";

export default {
  name: "FormsHeader",
  components: { FormTypeSwitch, VStatus, FormName },
  props: {
    formId: [Number, String],
  },
  watch: {
    info() {
      this.$store.commit(FormsMutations.UpdateFormInfo, {
        id: this.formId,
        data: { type: this.info.type },
      });
    },
  },
  computed: {
    name() {
      return this.$store.getters[FormsGetters.GetFormName](this.formId);
    },
    status() {
      return this.$store.getters[FormsGetters.GetFormStatus](this.formId);
    },
    type: {
      get() {
        return this.$store.getters[FormsGetters.GetFormType](this.formId);
      },
      set(type) {
        this.$store.commit(FormsMutations.ChangeFormsType, {
          type,
          id: this.formId,
        });
      },
    },
  },
};
</script>

<style scoped lang="scss">
.forms-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.forms-header__file-name-container {
  display: flex;
  align-items: center;
}
</style>
